@-webkit-keyframes spin {
  0% {
    -webkit-transform: translate(-50%, -50%) rotate(0deg);
    transform: translate(-50%, -50%) rotate(0deg); }
  100% {
    -webkit-transform: translate(-50%, -50%) rotate(360deg);
    transform: translate(-50%, -50%) rotate(360deg); } }

@keyframes spin {
  0% {
    -webkit-transform: translate(-50%, -50%) rotate(0deg);
    transform: translate(-50%, -50%) rotate(0deg); }
  100% {
    -webkit-transform: translate(-50%, -50%) rotate(360deg);
    transform: translate(-50%, -50%) rotate(360deg); } }

#database {
  max-width: 500px;
  text-align: center; }
  #database .execute-statement {
    padding-bottom: 10px;
    margin-bottom: 10px;
    border-bottom: 1px solid #f0f0f0; }
    #database .execute-statement textarea {
      padding: 5px;
      margin-bottom: 10px;
      width: 100%;
      max-width: 100%;
      height: 100px;
      border-radius: 5px;
      box-sizing: border-box; }
    #database .execute-statement button {
      display: block; }
  #database .content-wrapper {
    position: relative;
    min-height: 200px; }
    #database .content-wrapper .content {
      text-align: left; }
      #database .content-wrapper .content .results,
      #database .content-wrapper .content .no-results {
        margin: 10px 0px; }
      #database .content-wrapper .content .results {
        padding: 0px;
        list-style-type: none; }
        #database .content-wrapper .content .results li {
          display: inline-block;
          width: 100%;
          min-height: 40px;
          line-height: 30px;
          padding: 5px 0px;
          border-top: 1px solid #f0f0f0;
          box-sizing: border-box; }
          #database .content-wrapper .content .results li .button-wrapper {
            display: inline-block;
            float: right; }
            #database .content-wrapper .content .results li .button-wrapper .count {
              display: inline-block;
              margin: 0px 5px; }
            #database .content-wrapper .content .results li .button-wrapper .button {
              display: inline-block;
              width: 20px;
              height: 20px;
              padding: 2px;
              background-size: 20px;
              background-repeat: no-repeat;
              background-position: center;
              vertical-align: text-bottom;
              cursor: pointer; }
              #database .content-wrapper .content .results li .button-wrapper .button.hide {
                display: none; }
        #database .content-wrapper .content .results li:first-child {
          border-top: 0px; }
      #database .content-wrapper .content .no-results {
        line-height: 200px;
        font-size: 40px;
        font-weight: bold;
        text-align: center;
        opacity: 0.2; }
