@-webkit-keyframes spin {
  0% {
    -webkit-transform: translate(-50%, -50%) rotate(0deg);
    transform: translate(-50%, -50%) rotate(0deg); }
  100% {
    -webkit-transform: translate(-50%, -50%) rotate(360deg);
    transform: translate(-50%, -50%) rotate(360deg); } }

@keyframes spin {
  0% {
    -webkit-transform: translate(-50%, -50%) rotate(0deg);
    transform: translate(-50%, -50%) rotate(0deg); }
  100% {
    -webkit-transform: translate(-50%, -50%) rotate(360deg);
    transform: translate(-50%, -50%) rotate(360deg); } }

#table {
  text-align: center; }
  #table .content-wrapper {
    position: relative;
    min-height: 200px; }
    #table .content-wrapper .content {
      text-align: left; }
      #table .content-wrapper .content .execute-statement {
        padding-bottom: 10px;
        margin-bottom: 10px;
        border-bottom: 1px solid #f0f0f0; }
        #table .content-wrapper .content .execute-statement textarea {
          padding: 5px;
          margin-bottom: 10px;
          width: 100%;
          max-width: 100%;
          height: 100px;
          border-radius: 5px;
          box-sizing: border-box; }
        #table .content-wrapper .content .execute-statement button {
          display: block; }
      #table .content-wrapper .content .results,
      #table .content-wrapper .content .no-results {
        margin: 10px 0px; }
      #table .content-wrapper .content .results .count {
        display: inline-block; }
      #table .content-wrapper .content .results .page-settings {
        display: inline-block;
        float: right; }
        #table .content-wrapper .content .results .page-settings > * {
          vertical-align: middle; }
        #table .content-wrapper .content .results .page-settings span {
          display: inline-block;
          margin: 0px 10px; }
        #table .content-wrapper .content .results .page-settings select {
          margin-left: 10px; }
      #table .content-wrapper .content .results .table-content-wrapper {
        width: 100%;
        overflow: auto; }
        #table .content-wrapper .content .results .table-content-wrapper .table-content {
          margin: 20px 0px;
          width: 100%;
          border-collapse: collapse;
          font-size: 12px;
          clear: both; }
          #table .content-wrapper .content .results .table-content-wrapper .table-content th,
          #table .content-wrapper .content .results .table-content-wrapper .table-content td {
            padding: 5px;
            text-align: left;
            box-sizing: border-box;
            line-height: 13px; }
          #table .content-wrapper .content .results .table-content-wrapper .table-content thead th,
          #table .content-wrapper .content .results .table-content-wrapper .table-content tbody td {
            border: 1px solid #555555; }
          #table .content-wrapper .content .results .table-content-wrapper .table-content thead th {
            background-color: #f0f0f0; }
      #table .content-wrapper .content .no-results {
        line-height: 200px;
        font-size: 40px;
        font-weight: bold;
        text-align: center;
        opacity: 0.2; }
