@-webkit-keyframes spin {
  0% {
    -webkit-transform: translate(-50%, -50%) rotate(0deg);
    transform: translate(-50%, -50%) rotate(0deg); }
  100% {
    -webkit-transform: translate(-50%, -50%) rotate(360deg);
    transform: translate(-50%, -50%) rotate(360deg); } }

@keyframes spin {
  0% {
    -webkit-transform: translate(-50%, -50%) rotate(0deg);
    transform: translate(-50%, -50%) rotate(0deg); }
  100% {
    -webkit-transform: translate(-50%, -50%) rotate(360deg);
    transform: translate(-50%, -50%) rotate(360deg); } }

button {
  margin-top: 0px;
  padding: 5px 20px;
  background-color: #f0f0f0;
  border: none;
  border-bottom: 3px solid #a0a0a0;
  border-radius: 5px;
  font-size: 12px;
  text-align: center;
  vertical-align: top;
  cursor: pointer;
  outline: none; }
  button.secondary {
    background-color: transparent;
    border: 1px solid #a0a0a0; }
  button:active {
    margin-top: 2px;
    border-bottom: 1px solid #a0a0a0; }
    button:active.secondary {
      margin-top: 0px; }
  button[disabled] {
    opacity: 0.3;
    cursor: not-allowed; }

input,
textarea,
select {
  font-size: 12px; }

input {
  padding: 5px;
  width: 200px;
  height: 30px;
  border-radius: 5px;
  border-width: 1px;
  box-sizing: border-box;
  outline: none; }

a {
  text-decoration: none; }

.paper-wrapper {
  position: absolute;
  padding: 20px;
  top: 50%;
  left: 50%;
  width: 100%;
  max-height: 100%;
  transform: translate(-50%, -50%);
  box-sizing: border-box;
  overflow: auto; }
  .paper-wrapper .paper {
    position: relative;
    margin: auto;
    padding: 20px;
    box-sizing: border-box;
    background-color: #ffffff;
    border-radius: 5px;
    box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.2); }
    .paper-wrapper .paper .sign-out {
      position: absolute;
      right: 20px; }
    .paper-wrapper .paper .breadcrumb {
      position: absolute;
      left: 20px;
      font-weight: bold; }
      .paper-wrapper .paper .breadcrumb span {
        padding: 0px 5px; }
