@-webkit-keyframes spin {
  0% {
    -webkit-transform: translate(-50%, -50%) rotate(0deg);
    transform: translate(-50%, -50%) rotate(0deg); }
  100% {
    -webkit-transform: translate(-50%, -50%) rotate(360deg);
    transform: translate(-50%, -50%) rotate(360deg); } }

@keyframes spin {
  0% {
    -webkit-transform: translate(-50%, -50%) rotate(0deg);
    transform: translate(-50%, -50%) rotate(0deg); }
  100% {
    -webkit-transform: translate(-50%, -50%) rotate(360deg);
    transform: translate(-50%, -50%) rotate(360deg); } }

#no-connection,
#fields-required {
  width: 500px;
  text-align: center; }
  #no-connection img,
  #no-connection .logo,
  #fields-required img,
  #fields-required .logo {
    width: 200px;
    opacity: 0.5; }
  #no-connection input,
  #fields-required input {
    display: block;
    margin: 10px auto; }
  #no-connection #login-error,
  #fields-required #login-error {
    margin: 10px auto;
    height: 20px;
    line-height: 20px;
    color: #f44336; }

#login-logo {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 50%;
  transform: translate(-50%, -50%);
  opacity: 0.5; }
