@-webkit-keyframes spin {
  0% {
    -webkit-transform: translate(-50%, -50%) rotate(0deg);
    transform: translate(-50%, -50%) rotate(0deg); }
  100% {
    -webkit-transform: translate(-50%, -50%) rotate(360deg);
    transform: translate(-50%, -50%) rotate(360deg); } }

@keyframes spin {
  0% {
    -webkit-transform: translate(-50%, -50%) rotate(0deg);
    transform: translate(-50%, -50%) rotate(0deg); }
  100% {
    -webkit-transform: translate(-50%, -50%) rotate(360deg);
    transform: translate(-50%, -50%) rotate(360deg); } }

#databases {
  max-width: 500px;
  height: 100%;
  text-align: center; }
  #databases .content-wrapper {
    position: relative;
    min-height: 200px; }
    #databases .content-wrapper .content {
      text-align: left; }
      #databases .content-wrapper .content .results,
      #databases .content-wrapper .content .no-results {
        margin: 10px 0px; }
      #databases .content-wrapper .content .results {
        padding: 0px;
        list-style-type: none; }
        #databases .content-wrapper .content .results li {
          display: inline-block;
          width: 100%;
          min-height: 40px;
          line-height: 30px;
          padding: 5px 0px;
          border-top: 1px solid #f0f0f0;
          box-sizing: border-box; }
          #databases .content-wrapper .content .results li .button-wrapper {
            display: inline-block;
            float: right; }
            #databases .content-wrapper .content .results li .button-wrapper .button {
              display: inline-block;
              width: 20px;
              height: 20px;
              padding: 2px;
              background-size: 20px;
              background-repeat: no-repeat;
              background-position: center;
              vertical-align: text-bottom;
              cursor: pointer; }
              #databases .content-wrapper .content .results li .button-wrapper .button.hide {
                display: none; }
        #databases .content-wrapper .content .results li:first-child {
          border-top: 0px; }
      #databases .content-wrapper .content .no-results {
        line-height: 200px;
        font-size: 40px;
        font-weight: bold;
        text-align: center;
        opacity: 0.2; }
